<template>
  <div>
    <v-app-bar color="primary" dark>
      <!-- Buttons of navigation items -->
      <div class="d-none d-md-flex flex-row">
        <div v-for="(item, index) in navItems" :key="index">
          <span class="hover">
            <v-btn
              @click="$emit('goTo', item.href)"
              text
              height="53"
              class="nav-btn px-3"
              >{{ item.title }}</v-btn
            ></span
          >

          <!-- The square icon to separate items -->
          <v-icon v-if="index != navItems.length - 1" x-large color="#90A4AE"
            >mdi-square-small</v-icon
          >
        </div>
      </div>

      <!-- Menu icon - It will be shown on the MOBILE device -->
      <div @click.stop="drawer = !drawer" class="d-md-none d-block pointer">
        <v-icon>mdi-menu</v-icon>
      </div>

      <v-spacer></v-spacer>

      <!-- The switch button for light/dark mode -->
      <div
        @click="changeThemeMode"
        class="pointer white--text d-flex flex-row ps-1"
      >
        <v-icon class="nav-icon" v-if="$vuetify.theme.dark">$sun</v-icon>
        <v-icon class="nav-icon" v-else>$moon</v-icon>
      </div>
    </v-app-bar>

    <!-- Sidebar navigation - It will be shown on the mobile device -->
    <v-navigation-drawer v-model="drawer" absolute temporary>
      <v-list nav dense>
        <div
          @click="
            $emit('goTo', 'profile');
            drawer = false;
          "
          class="header bgGrey d-flex flex-row w-100 py-4"
        >
          <div class="image-box mx-3">
            <img src="/parnia.jpg" />
          </div>
          <div>
            <p class="name mt-3 mb-2 mainFont--text">Parnia Maroofi</p>
            <p class="position greyColor--text mb-0">Front-End Developer</p>
          </div>
        </div>
        <v-list-item-group
          v-for="(item, index) in navItems"
          :key="index"
          class="mx-1"
        >
          <v-list-item
            @click="
              $emit('goTo', item.href);
              drawer = false;
            "
          >
            <v-list-item-title
              ><v-icon class="pb-1">{{ item.icon }}</v-icon
              ><span class="mx-6">{{ item.title }}</span></v-list-item-title
            >
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      navItems: [
        {
          title: "About me",
          icon: "mdi-account-outline",
          href: "about",
        },
        {
          title: "Activities",
          icon: "mdi-text-box-outline",
          href: "activities",
        },
        {
          title: "Education",
          icon: "mdi-school-outline",
          href: "education",
        },
        {
          title: "Work Experiences",
          icon: "mdi-briefcase-outline",
          href: "workExperiences",
        },
        {
          title: "Skills",
          icon: "mdi-laptop",
          href: "skills",
        },
      ],
    };
  },
  methods: {
    changeThemeMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("dark", this.$vuetify.theme.dark);
    },
  },
};
</script>
