<template>
  <div>
    <div class="page-header" :style="background()">
      <!-- The button to download resume PDF -->
      <div class="download-btn">
        <v-btn :loading="downloading" @click="$root.$emit('generateReportEvent')">
          <template v-slot:loader>
            <span class="pdf-btn d-flex white--text">
              downloading
              <div class="snippet ms-3 mt-2" data-title=".dot-elastic">
                <div class="stage">
                  <div class="dot-elastic"></div>
                </div>
              </div>
            </span>
          </template>
          <span class="pdf-btn">Download Resume</span></v-btn
        >
      </div>

      <div class="text-box1 animate__animated animate__fadeInDown">Parnia Maroofi - Programmer</div>

      <div class="text-box2 animate__animated animate__fadeInLeft">Front-End developer</div>

      <div class="text-box3 animate__animated animate__fadeInUp">Resume Website</div>

      <!-- Social media icons -->
      <div class="icons-box">
        <!-- Github Icon -->
        <a target="blank" href="https://github.com/parniamaroofi">
          <div class="pointer">
            <v-icon>mdi-github</v-icon>
          </div>
        </a>
        <!-- Linkedin Icon -->
        <a target="blank" href="https://www.linkedin.com/in/parniamaroofi">
          <div class="pointer">
            <v-icon>mdi-linkedin</v-icon>
          </div>
        </a>
        <!-- Gmail Icon -->
        <div @click="ShowGmailAddress()" class="pointer">
          <v-icon>mdi-gmail</v-icon>
        </div>
      </div>

      <!-- Gmail address snackbar -->
      <v-snackbar v-model="gmailSnackbar" vertical shaped :timeout="-1" color="primary">
        <v-icon class="pr-3">$gmail</v-icon
        ><span style="letter-spacing: 1px"> {{ gmailAddress }}</span>

        <template v-slot:action="{ attrs }">
          <v-btn
            v-clipboard:copy="gmailAddress"
            v-clipboard:success="onCopy"
            :disabled="copied"
            color="info lighten-2"
            text
          >
            <span v-if="!copied">Copy</span>
            <span v-else class="green--text">Copied to clipboard!</span>
          </v-btn>
          <v-btn
            color="red lighten-2"
            text
            v-bind="attrs"
            @click="gmailSnackbar = false"
            v-if="!copied"
          >
            <span>Close</span>
          </v-btn>
        </template>
      </v-snackbar>

      <!-- Mouse wheel sign -->
      <div @click="$emit('goTo', 'profile')" id="mouse-btn" class="pointer">
        <span class="mouse d-sm-flex d-none">
          <span class="mouse-wheel"> </span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from './../event-bus.js';
export default {
  data() {
    return {
      copied: false,
      downloading: false,
      gmailSnackbar: false,
      gmailAddress: 'parniamaroofi@gmail.com',
    };
  },
  methods: {
    background() {
      if (!this.$vuetify.theme.dark) {
        return 'background-image: url("/5.jpg")';
      } else {
        return 'background-image: url("/1.jpg")';
      }
    },
    onCopy() {
      this.copied = true;
      setTimeout(() => {
        this.gmailSnackbar = false;
      }, 2000);
    },
    ShowGmailAddress() {
      this.copied = false;
      this.gmailSnackbar = true;
    },
  },
  mounted() {
    EventBus.$on('download-event', (downloading) => {
      this.downloading = downloading;
    });
  },
};
</script>
