<template>
  <div id="resume" class="bgColor" @scroll="computedDownButton">
    <!-- NAV -->
    <Navigation @goTo="goTo" id="pageTop" />

    <!-- Page header -->
    <Header @goTo="goTo" />

    <!-- container -->
    <div class="container">
      <v-row>
        <v-col lg="3" md="12" sm="12" cols="12">
          <Profile style="height: 100%" />
        </v-col>
        <v-col>
          <!-- About me section -->
          <AboutMe />

          <!-- Activities section -->
          <Activities />

          <!-- Education section -->
          <Education />

          <!-- Work Experiences section -->
          <Experiences />

          <!-- Soft skills section -->
          <SoftSkills />
        </v-col>
      </v-row>
    </div>

    <!-- footer -->
    <Footer />

    <!-- pdf -->
    <Html2pdf />

    <div class="goUpBtn">
      <Transition name="bounce">
        <v-btn
          v-if="showBtn"
          @click="goTo('pageTop')"
          outlined
          class="secondary"
          ><v-icon class="white--text">mdi-chevron-up</v-icon></v-btn
        >
      </Transition>
    </div>
  </div>
</template>
<script>
import Navigation from "./../src/components/Navigation.vue";
import Header from "./../src/components/Header.vue";
import Profile from "./../src/components/Profile.vue";
import AboutMe from "./../src/components/AboutMe.vue";
import Activities from "./../src/components/Activities.vue";
import Education from "./../src/components/Education.vue";
import Experiences from "./../src/components/Experiences.vue";
import SoftSkills from "./../src/components/SoftSkills.vue";
import Footer from "./../src/components/Footer.vue";
import Html2pdf from "./../src/components/Html2pdf.vue";
export default {
  components: {
    Navigation,
    Header,
    Profile,
    AboutMe,
    Activities,
    Education,
    Experiences,
    SoftSkills,
    Footer,
    Html2pdf,
  },
  data() {
    return {
      showBtn: false,
    };
  },
  methods: {
    goTo(id) {
      document.getElementById(id).scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    },
    computedDownButton() {
      var container = document.querySelector("#resume");
      // var scrollHeight = container.scrollHeight;
      if (container.scrollTop > 250) {
        this.showBtn = true;
      } else {
        this.showBtn = false;
      }
    },
  },
  mounted() {
    if (localStorage.getItem("dark")) {
      this.$vuetify.theme.dark = JSON.parse(localStorage.getItem("dark"));
    }
  },
};
</script>

<style>
.bounce-enter-active {
  animation: bounce-in 0.3s;
}
.bounce-leave-active {
  animation: bounce-in 0.3s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
</style>
